import { useState } from 'react';
import { CloudUrl } from '../../../constants';
import { APPHOST, isSocialWall } from '../../../actions/api';
import './Upgrade.scss';

export const VERIFY_IDS = [26032, 27439, 27454, 26911, 390, 25877];

const SocialWallBranding = ({ appData }) => {
    const { wall } = appData;
    const [isShow, setIsShow] = useState(false);

    const onCreateCookies = event => {
        try {
            var date = new Date();
            date.setTime(date.getTime() + (1 * 60 * 60 * 1000));
            document.cookie = "planUpgrade=" + date.toUTCString() + "; path=/; expires=" + date.toUTCString();
        } catch (e) {
        }
    };

    const upgradeUrl = (wall && parseInt(wall.Wall.owner) && VERIFY_IDS.includes(wall.Wall.owner)) ? true : false;

    return <><div className={`tb_ug_m ${isShow ? 'tb_show_' : 'tb_hide_'}`}>
        <div className="tb_ug_m_w">
            <div onClick={event => setIsShow(false)} className="tb_ug_close">
                <div className="tb__icon tb-close-thin"><div></div></div>
            </div>
            <div className="tb_ug_body">
                <div className="tb_ug_img" style={{ backgroundImage: `url(${CloudUrl}/media/error/upgrade.svg)` }}>
                    <div className="tb_ug_title">Remove Branding</div>
                </div>
                <div className="tb_ug_s_title">
                    Wish to upgrade your plan to remove Taggbox's branding?
                </div>
                <div className="tb_ug_btn_w">
                    <a target="_blank" className="tb_ug_btn" href={upgradeUrl ? `${APPHOST}events` : isSocialWall ? "https://socialwalls.com/price/" : "https://taggbox.com/price/display-price/"} onClick={upgradeUrl ? onCreateCookies : null}>Upgrade Now!</a>
                </div>
            </div>
        </div>
    </div>
        <div className={`tb_ug_overlay__ ${isShow ? 'tb_show_' : ''}`}></div>

        {!isShow ? <div onClick={event => setIsShow(true)} style={{ cssText: 'background-color: rgba(250, 250, 250, 1) !important; align-items: center !important; display: flex !important; width: auto !important; max-width: 200px !important; text-decoration: none !important; padding: 6px 20px !important; border-radius: 45px !important; color: #0b57d0 !important; position: fixed !important; right: 15px !important; bottom: 15px !important; z-index: 999999999 !important; cursor: pointer !important; box-shadow: rgba(255, 64, 81, 0.35) 0px 3px 6px !important;' }}>
            <img src={`${CloudUrl}/media/logo/socialwalls-branding.svg`} alt="Socialwall" style={{ display: 'block', maxWidth: '100%' }} />
        </div> : null}
    </>
};

export default SocialWallBranding;
